/* http://meyerweb.com/eric/tools/css/reset/
   v5.0.1 | 20191019
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, menu, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block;
}

/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
  display: none;
}

body {
  line-height: 1;
}

menu, ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
}

* {
  box-sizing: border-box;
}

.srOnly {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
}

.wrapper {
  min-height: 100vh;
  background: url(bg-mobile.svg) center top no-repeat, #674baf;
  background-size: 100%;
}
@media (min-width: 75rem) {
  .wrapper {
    background: url(bg-desktop.svg) left center no-repeat, #674baf;
    background-size: auto 100%;
  }
}

.container {
  max-width: 54rem;
  margin: 0 auto;
  padding: 3.8rem 3.8rem 4rem;
  overflow: hidden;
}
@media (min-width: 64rem) {
  .container {
    max-width: 80rem;
    padding: 8rem 4rem;
  }
}
@media (min-width: 75rem) {
  .container {
    display: grid;
    grid-column-gap: 4.1vw;
    grid-template: "header header" auto "hero content" 1fr "hero footer" auto/48.334vw 36.44vw;
    max-width: unset;
    height: 100vh;
    padding: 8vh 5.556vw 5vh;
  }
}

@media (min-width: 75rem) {
  .header {
    grid-area: header;
  }
}

.logo {
  width: 12rem;
  height: 1.9rem;
  margin-left: 0.2rem;
  background: url(logo.svg) center no-repeat;
  background-size: contain;
}
@media (min-width: 48rem) {
  .logo {
    width: 16.4rem;
    height: 2.6rem;
  }
}
@media (min-width: 64rem) {
  .logo {
    width: 20rem;
    height: 3.2rem;
    margin: 0;
  }
}

.heroImage {
  width: 100%;
  height: 0;
  margin-top: 6.8rem;
  padding-top: 70.9%;
  background: url(illustration-mockups.svg) center no-repeat;
  background-size: 100% 100%;
  transform: scale(1.02);
}
@media (min-width: 75rem) {
  .heroImage {
    grid-area: hero;
    margin-top: 13vh;
  }
}

.content {
  margin-top: 6.1rem;
  text-align: center;
}
@media (min-width: 75rem) {
  .content {
    grid-area: content;
    margin-top: 17.5vh;
    text-align: left;
  }
}

.heading {
  margin: 0;
  color: white;
  font: 700 2.4rem Poppins, san-serif;
  line-height: 150%;
  letter-spacing: -0.01em;
  text-transform: capitalize;
}
@media (min-width: 48rem) {
  .heading {
    font-size: 3.2rem;
  }
}
@media (min-width: 64rem) {
  .heading {
    font-size: 4rem;
  }
}

.text {
  margin: 1.3rem 0 0;
  color: white;
  font: 400 1.6rem/150% Open Sans, sans-serif;
}
@media (min-width: 64rem) {
  .text {
    margin-top: 2rem;
    font-size: 1.8rem;
  }
}

.button {
  width: 66.9%;
  margin-top: 2.4rem;
  padding: 1.1rem 3.2rem 0.9rem;
  color: #674baf;
  font: 400 1.3rem Poppins, sans-serif;
  letter-spacing: -0.05em;
  background: white;
  border: none;
  border-radius: 2rem;
  box-shadow: 0.1rem 0.3rem 0.5rem rgba(0, 0, 0, 0.3);
  cursor: pointer;
  transition: 0.2s;
}
@media (min-width: 48rem) {
  .button {
    width: unset;
    font-size: 1.5rem;
    border-radius: 2.1rem;
  }
}
@media (min-width: 64rem) {
  .button {
    padding: 1.3rem 6.4rem 1.6rem;
    font-size: 1.8rem;
    letter-spacing: normal;
    border-radius: 2.8rem;
  }
}
.button:focus {
  outline: none;
  box-shadow: 0 0 0 0.4rem #3d2c68;
}
.button:hover {
  box-shadow: 0.2rem 0.4rem 0.6rem rgba(0, 0, 0, 0.4);
}
.button:active {
  background: #c0b4df;
}

.footer {
  grid-area: footer;
  margin-top: 6.4rem;
}
@media (min-width: 75rem) {
  .footer {
    margin-top: 3.2rem;
  }
}

.list {
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 75rem) {
  .list {
    justify-content: flex-end;
  }
}

.item {
  display: block;
}
.item:not(:first-child) {
  margin-left: 1rem;
}
@media (min-width: 48rem) {
  .item:not(:first-child) {
    margin-left: 1.6rem;
  }
}

.socialLink {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2.8rem;
  height: 2.8rem;
  background: transparent;
  border-radius: 50%;
  box-shadow: inset 0 0 0 0.1rem white;
  transition: 0.2s ease-in-out;
}
@media (min-width: 48rem) {
  .socialLink {
    width: 4rem;
    height: 4rem;
  }
}
.socialLink:focus, .socialLink:hover {
  background: white;
  outline: none;
}
.socialLink:focus .icon, .socialLink:hover .icon {
  color: #674baf;
}
.socialLink:active {
  background: #c0b4df;
}

.icon {
  display: block;
  width: 50%;
  height: 50%;
  color: white;
}